.about {
  min-height: fit-content;
  position: relative;
}

.about .line-styling {
  position: absolute;
  top: 50px;
  left: 50%;
  transform: translateX(-50%);
  height: 50px;
  width: 85%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.about .line-styling .style-circle {
  width: 10px;
  height: 10px;
  border-radius: 50%;
}

.about .line-styling .style-line {
  width: 95%;
  height: 5px;
  border-radius: 10px;
}

.about-body {
  display: grid;
  grid-template-columns: 60% 40%;
  padding-top: 8rem;
  height: fit-content;
  overflow-x: hidden;
}

.about-description {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-evenly;
  flex: 0.5;
  width: 100%;
  height: fit-content;
  overflow-x: hidden;
}

.about-description > h2 {
  margin-bottom: 40px;
  font-size: 2.5rem;
  font-family: var(--primaryFont);
}

.about-description > p {
  font-size: 1.25rem;
  color: #e0d9d9;
  font-family: var(--primaryFont);
}



.about-animation {
  pointer-events: none;
  display: flex;
  justify-content: center;
}

@media (min-width: 992px) and (max-width: 1380px) {
  .about-description > h2 {
    font-size: 3.3rem;
    margin-bottom: 30px;
  }
  .about-description > p {
    font-size: 1.15rem;
  }

  /* .about-img > img {
  } */
}

.vertical-timeline {
  width: 95%;
  max-width: 1170px;
  margin-left: 115px;
  position: relative;
  padding: 0.001em 0;
}

@media screen and (max-width: 992px) {
  .about .line-styling {
    top: 20px;
  }

  .about-body {
    padding-top: 2rem;
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    justify-content: space-evenly;
  }

  .about-description {
    flex: 0.6;
    box-sizing: border-box;
    padding: 20px;
  }

  .about-description > h2 {
    font-size: 3rem;
    margin-bottom: 20px;
  }

  .about-description > p {
    font-size: 1.1rem;
  }

  /* .about-img > img {
 
  } */
}

@media screen and (max-width: 800px) {
  .about .line-styling .style-circle {
    width: 8px;
    height: 8px;
    border-radius: 50%;
  }
  .about-description > h2 {
    align-self: center;
  }
}

@media screen and (max-width: 767px) {
  .content {
    width: 100%;
    margin-right: 1rem;
  }

  .about-description > h2 {
    font-size: 2.5rem;
    text-align: center;
  }

  .about-description > p {
    font-size: 1rem;
    text-align: center;
  }

  .vertical-timeline-element-content {
    padding: 1em !important;
  }

  .vertical-timeline-element-date {
    font-size: 0.85rem;
  }

  .vertical-timeline-element-title {
    font-size: 1.25rem;
  }

  .vertical-timeline-element-subtitle {
    font-size: 1rem;
  }

  .bullet-point {
    font-size: 0.85rem;
  }

  .label {
    font-size: 0.85rem;
  }
}
@media screen and (max-width: 600px) {
  .about .line-styling .style-circle {
    width: 5px;
    height: 5px;
    border-radius: 50%;
  }
  .about .line-styling .style-line {
    width: 93%;
    height: 3px;
    border-radius: 10px;
  }

  .about-description > h2 {
    font-size: 2.5rem;
  }

  .about-description > p {
    font-size: 0.95rem;
  }

  /* .about-img > img {
  } */
}

.label {
  display: inline-block;
  padding: 8px 16px;
  margin: 5px;
  font-size: 16px;
  font-weight: bold;
  color: white;
  background-color: #007bff; /* Blue color */
  border: none;
  border-radius: 20px;
  cursor: default;
  text-align: center;
  text-decoration: none;
}

.h1--tag {
  font-weight: 600;
  font-size: 2.25rem;
  line-height: 110%;
  margin-top: 3rem;
  margin-left: 60px;
}

.h4--tag {
  font-weight: 400;
  font-size: 1.75rem;
  line-height: 80%;
  margin: 1rem 0;
  margin-left: 60px;
}

.bullet-points {
  margin-top: 10px; /* Add margin to separate bullet points from other content */
}

.bullet-point {
  display: flex; /* Use flexbox to align bullet icon and bullet point text */
  align-items: flex-start; /* Align items to the start of the flex container */
  margin-bottom: 5px; /* Add margin between each bullet point */
}

.bullet-icon {
  margin-right: 5px; /* Add margin between bullet icon and bullet point text */
  font-size: 25px; /* Adjust font size of the bullet icon */
}

.bullet-text {
  font-size: 1 rem; /* Adjust font size of the bullet point text */
  line-height: 1.5; /* Ensure consistent line height for multi-line text */
  text-align: justify;
}
